.App {
    font-family: sans-serif;
    text-align: center;
    padding-left: 50px;
    padding-top: 35px;
  }
  .myCustomCarousel .slick-arrow {
    color: #070606 !important;
    border: none;
    outline: none;
    background: transparent !important;
  }
  .slick-prev {
    left: 3% !important;
    z-index: 1;
  }
  .slick-next {
    right: 3% !important;
    z-index: 1;
  }
  
